        {id:7,bg_class:'2',sm_class:'2',title:'“彩虹之国”南非7',time:'2019-10-25 15:14',img_url:'/static/img/article/7.jpg',text:'来到2019年，持续第六年的鳗荒也不见好转。<br>由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。<br>产量变少，随之而来的是价格的飞涨。目前鳗鱼的价格已经突破每公斤5000日元（人民币320元）。<br>自从日本鳗鲡在2014年登上IUCN（世界自然保护联盟）濒危名单以来，全球范围内的鳗鱼自由，似乎已经离我们越来越远。<br><img src="/static/img/article/7.jpg" alt="加载中"><br>鳗鱼，是一条跨越东亚三国的鱼<br>提起滑溜溜的鳗鱼，很多人的第一反应是隔壁岛国那碗香喷喷、金灿灿的鳗鱼饭。实际上，长相差不多的蒲烧鳗鱼在关东、关西两地也有不同的演绎方式。<br>从活杀鳗鱼开始，关东、关西料理法就走上了不同的道路。关东受武士文化影响，避讳切腹，从背部开始杀鱼，而关西料理则直接从腹部切开鳗鱼。<br>关东人偏爱清淡柔软的口感，会将鳗鱼先烤、再蒸、再烤，去除鱼脂的油腻，口感更为清爽柔和。而关西人则直接串起活鳗生烤，在炭火上烤干皮间油脂，使鳗鱼表皮更酥脆。<br>无论是哪种烤制方式，鳗鱼饭的灵魂一定在于店家的秘制酱汁。日本有名的鳗鱼老店比比皆是，传承百年的自家酱汁就是它们专属的差异化商标。<br>鳗鱼酱汁的组成都是鳗鱼骨、酱油、味噌、糖，但微妙的比例差别就产生了让食客难以忘怀的独特风味。<br>在炭火烤制之外，鳗鱼冻、鳗鱼玉子卷等口味清爽的小吃也别有风味。而且，鳗鱼的全身都在日本人的厨房里得到善用。<br><img src="/static/img/article/5.jpg" alt="加载中"><br>鱼肠、鱼鳔富含胶原蛋白，满口馥郁甘香；鳗肝稍有苦涩，制成鳗肝汤清苦去腻；炸鳗骨仅以一撮薄盐吊味，酥脆咸鲜，是小酌良伴。<br>鳗鱼在日本声名最盛，但鳗鱼早在一千多年前就已经加入中国人的河鲜豪华套餐，做法也丰富得多。长江流域以南的鳗鱼称为白鳝，因为体型大气，肉质紧实，常担当盛宴中压轴大菜的重任。<br>知名度最高的做法，非造型凹得最好的豉汁盘龙鳝莫属。<br>粗壮的白鳝切肉不断骨，像潜龙一样盘在大圆盘中，以浓郁厚重的豉汁大火蒸制。洁白的鱼肉弹牙紧致，酱料咸中带微辣，引出新鲜白鳝的鲜活之味。<br>爱吃鱼的顺德人也不负众望，在白鳝身上也钻研出许多刁钻的花样。<br>旧时特供富贵人家的顶骨鳝，将新鲜鳝鱼切至骨断皮连，油煎去腥，然后以过油的烧肉、蒜头焖煮。<br>刚一熟透，即原条取出以筷子去骨，用上等火腿替换，再用猪网油包住白鳝、烧肉、火腿、陈皮等丰富的食材蒸制半小时，出锅时肉香和鲜香交汇融合，味道层次分明，丰腴浓厚。'},
