  .item div{
    width: 100%;
    box-sizing: border-box;
    padding: .5rem ;
  }
 
  .item div a{
    display: inline-block;
    background: #fafafa;
    padding: .5rem;
    border-radius: 3px;
    position: relative;
    
  }
  .a-img{
    overflow: hidden;
  }
  .a-img img{
    display: block;
    transition: all .5s;
  }
  
  .a-title{
    font-size: 1rem;
    line-height: 2;
    color: #333;    
    font-weight: bold;
    letter-spacing: 1px;
    transition: all .3s;
  }
  .a-time{
    font-size: .8rem;
    line-height: 1.5;
    color: #f00;    
    padding: 0 1rem;
    letter-spacing: 1px;
  }
  .a-text{
    padding: 0 1rem;
    color: #666;
    line-height: 1.5;
    font-size: .8rem;
    letter-spacing: 1px;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    transition: all .3s;
  }
  .a-gauze{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-box-shadow: rgba(0,0,0,0.5) 0px .3rem .3rem;
    -moz-box-shadow: rgb(0,0,0,0.5) 0px .3rem .3rem;
    box-shadow: rgba(0,0,0,0.5) 0px .3rem .3rem;
    opacity: 0;
    transition: all .5s;
    z-index: -1;
  }
  .item div:hover a .a-gauze{
    opacity: 1;
  }
  .item div:hover a .a-title{
    color: #f00;
  }
  .item div:hover a .a-text{
    color: #000;
  }
  .item div:hover a .a-img img{
    transform: scale(1.1);
  } 
  @media (max-width: 1200px) {
    .person{
      display: none;
    }
    .sub-text{
      width: 100%;
    }
  }
  @media (max-width: 750px) {
  .sub-content{
    padding: 10px 0;
  }
  .swiper-video{
    padding: 10px 10px;
    background: #fafafa;
    animation: bounceInDown .5s;
  }
  .item{
    width: 100%;
  }
  }
</style>



